import now from '../../../getTime.js';
export default {
	data() {
		return {
			index: -1,
			picker: ['丁桥校区', '翠苑校区'],
			idRow: ['S000099100001', 'S000099100002'],
			schoolId: 'S000099100001',
			dataRow: [],
			modalName: null,
			modalContent: '',
			yhname: '',
			start: '',
			end: '',
			now: '',
			isCurrent: false,
			checked: 0,

			wait: 0,

			day: {
				
				today: true,
				yestoday: false
			},
			
			today: '',
			yestoday: '',
			
			endTime: ''
		};
	},
	methods: {
		ChooseDay(num) {
			 if (num == 2) {
				this.day.today = true;
				this.day.now = this.day.yestoday = false;
				//获取今天数据
				this.dataRow = [];
				
				
				this.checked = this.wait = 0;
				
				this.PickerChangeItem(2)
			} else {
				this.day.yestoday = true;
				this.day.now = this.day.today = false;
				this.dataRow = [];
				
				
				this.checked = this.wait = 0;
				
				//获取昨天数据
				this.PickerChangeItem(3)
			}
		},
		showModel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
					}
				}
			});
		},
		
		
		showDetail(e) {
			console.log(e);
			uni.navigateTo({
				url: 'BCdetail/BCdetail?placeNo=' + e.currentTarget.dataset.info.placeNo + '&time=' + e.currentTarget.dataset.info.checkTime
			});
		},
		hideModal(e) {
			this.modalName = null;
		},
		PickerChangeItem(num) {
			var time;
			if (num == 2) {
				time = this.today
			} else {
				time = this.yestoday
			}
			// /Web/Get_query_round_xg?campusplaceNo=S000099100001&type=XG&time=' + time
			uni.showLoading({
				title: '查询中。。。',
				mask: true
			});
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/web/Get_query_CheckPoint',
				data: {
					campusplaceNo: self.schoolId,
					type: 'PC',
					endTime: time
				}
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading();
				if (res.data.code == 0) {
					var info = res.data.data;
					if (info.length > 0) {
						self.dataRow = info;
						for (let s of info) {
							if (s.isSafe == 'True') {
								self.checked  = self.checked + 1;
							} else {
								self.wait = self.wait+1;
							}
						}
					} else {
						self.showModel('该天没有记录')
					}
					
				} else {
					self.showModel('查询失败')
				}
			})
		},
		
	},
	mounted() {
		
		console.log(now.yestoday(-1, '-'));
		this.today = now.now();
		// this.today = '2019-12-19'
		this.yestoday = now.yestoday(-1, '-');
		
		this.PickerChangeItem(2)
	},
	created() {
		
	}
};
